import { DINGDING_LOGIN, GITHUB_LOGIN, USER_LOGOUT, RECEIVE_USER_INFO } from './action-types';
import cookie from 'react-cookies'


const initState = { userInfo: { errcode: 0 } };

function login(state = initState, action) {
  switch (action.type) {
    //loginçç¶æè§¦åç±receiveå®æ
    case DINGDING_LOGIN: 
      return state;
    case GITHUB_LOGIN:
      return state;
    case USER_LOGOUT:
      //å é¤ç¼å­çé¢cookieï¼æç¶æè®¾ç½®ä¸ºåå§å¼
      cookie.remove('loginCookie', { path: '/' });
      return { ...initState };
    case RECEIVE_USER_INFO:
      // è¿éçcaseè¦ç¨å¤§æ¬å·ï¼é²æ­¢åéç©¿éï¼ç±eslintæ¥éæé
      {
        let ret = { ...action.data };
        if (action.data.errcode !== undefined) {
          ret.userInfo.errcode = action.data.errcode;
        }
        else {
          ret.userInfo.errcode = 0;
        }
        return { ...ret };
      }
    default:
      return { ...state };
  }
}

export default login;